@import '../base/variables';

.related-post {
    position: relative;

    display: inline-block;  margin-bottom: 10px;     background-color: @background-color-3;
    .related-post__inner-container {
        position: relative;  display: block;

        &:hover .related-post__meta-container {
            background-color: @background-color-2;
        }
    }

    .related-post__image {
        min-width: 100%;  max-width: 100%;         min-height: 100%;
        max-height: 100%;
    }

    .related-post__meta-container {
        position: absolute;  bottom: 0;  left: 0;

        box-sizing: border-box;  width: 100%;  margin-bottom: 6px;  padding: 10px;  background: rgba(0, 0, 0, .5);
    }

    .related-post__title-container {
        text-align: center;
    }

    .related-post__title {
        font-family: @main-control-font;  font-size: 16px;  font-weight: bold;  text-transform: uppercase;  color: @text-color-2;
    }

    .related-post__date-container {
        text-align: center;
    }

    .related-post__date {
        font-family: @main-control-font;  font-size: 16px;  font-weight: normal;  color: @text-color-2;
        [class*='fa-'] {
            padding: 5px;
        }
    }
}
