@import '../base/variables';

.rpbt_shortcode {
     > h3 {
        display: none;
    }
}

.related-gallery {
    @media @medium-screen, @small-screen {
        display: flex;  flex-wrap: wrap;  justify-content: space-between;
    }

    .related-gallery__item-container {
        box-sizing: border-box;  padding-bottom: 10px;  text-align: center;
        @media @medium-screen {
            padding: 10px;  flex-basis: 50%;
        }

        @media @small-screen {
            padding: 10px;  flex-basis: 100%;
        }
    }
}

