@import '../base/variables';
@import '../base/layout';

.header {
    background-color: @background-color-1;

    .header__inner-container {
        margin: 0 auto;  padding: 10px 50px;
        @media @medium-screen {
            padding: 10px 10px 50px;
        }

        @media @small-screen {
            padding: 10px;
        }

        &:after {
            display: table;  clear: both;  content: '';
        }
    }

    .header__logo-container {
        float: left;  max-width: 400px;  padding-bottom: 6px;         text-align: center;

        @media @xl-screen {
            margin-top: 30px;
            padding-bottom: 13px;  text-align: left;
        }

        @media @medium-screen {
            padding-bottom: 13px;  text-align: left;
        }

        @media @small-screen {
            float: none;  max-width: none;
        }
    }

    .header__search-container {
        float: right;  text-align: right;

        @media @small-screen, @medium-screen {
            display: block;  float: none;             width: 100%;  margin-bottom: 10px;
        }
    }

    .header__socials-container {
        float: right;  text-align: right;

        @media @small-screen {
            display: none;
        }
    }

    .header__menu-container {
        float: right;  clear: right;  max-width: 600px;
        @media @xl-screen {
            max-width: 650px;
        }

        @media @medium-screen {
            display: block;  float: none;  max-width: none;
        }

        @media @small-screen {
            display: block;  float: none;  max-width: none;
        }
    }

    .header__logo {
        display: block;  max-height: 90px;  margin: 0 auto;  padding-bottom: 15px;

        @media @medium-screen, @xl-screen {
            float: left;  padding-right: 15px;
        }
    }

    .header__logo-text {
        display: inline-block;  padding-bottom: 10px;  font-family: @main-control-font;  color: @text-color-2;

        &.header__logo-text--primary {
            font-size: 18px;  font-weight: bold;  text-transform: uppercase;
            @media @xl-screen {
                font-size: 24px;
            }
        }

        &.header__logo-text--secondary {
            font-size: 12px;

            @media @small-screen, @large-screen {
                display: none;
            }
        }
    }
}
