@import '../base/variables';

.indicator(@background-color, @border) {
    &:after {
        position: absolute;  bottom: -7px;  left: 45%;  display: block;  width: 8px;  height: 8px;  content: '';  border: @border;  border-radius: 50%;  background-color: @background-color;
        @media @small-screen {
            display: none;
        }
    }
}

.menu-navigation-container {
    #menu-navigation {
        display: table;  width: 100%;  list-style: none;

        @media @small-screen {
            display: block;
        }
    }

    .menu-item {
        .indicator(@background-color-1, 2px solid white);  position: relative;  display: table-cell;  padding: 5px;  text-align: center;  border-bottom: 1px solid white;

        &:hover {
            .indicator(@border-color-1, 1px solid white);
        }

        &.current-menu-item {
            .indicator(@text-color-3, 2px solid @text-color-3);
        }

        a {
            display: inline-block;  box-sizing: border-box;             width: 100%;  height: 100%;  padding: 10px;  font-family: @main-control-font;  font-size: 14px;  vertical-align: middle;  text-decoration: none;  text-transform: uppercase;  color: @text-color-2;
        }

        @media @xl-screen {
            a {
                font-size: 18px;
            }
        }

        @media @large-screen, @xl-screen {
            min-width: 100px;
        }

        @media @medium-screen {
            min-width: 0;  padding-right: 15px;  padding-left: 15px;
        }

        @media @small-screen {
            display: block;

            min-width: 0;  margin: 10px 0;             border: 1px solid white;
        }
    }
}

.mobile-menu {
    .mobile-menu__main-button {
        position: relative;  padding: 15px;  font-family: @main-control-font;  font-size: 14px;  font-weight: normal;

        cursor: pointer;  text-align: center;  text-transform: uppercase;  color: white;         border: 1px solid white;
    }

    .fa {
        position: absolute;  top: 14px;  left: 10px;  font-size: 24px;
    }
}
