@import 'variables';

html, body {
    height: 100%;  background: @border-color-1;
}

.layout {
    position: relative;  box-sizing: border-box;
    width: 100%;  min-width: @mobile-min-width;  max-width: @layout-max-width * 1;     min-height: 100%;  margin: 0 auto;  padding-bottom: 220px;
}

.layout__service {
    box-sizing: border-box;  margin: 0 auto;
    @media @small-screen, @medium-screen {
        min-width: @mobile-min-width;
    }
}

.layout__content {
    box-sizing: border-box;
    margin: 0 auto;  padding: 90px 20px 70px;
    @media @medium-screen, @large-screen, @xl-screen {
        max-width: @large-content-layout-width;
    }

    @media @small-screen {
        min-width: @mobile-min-width;  padding-bottom: 90px;
    }
}

.visibility-layout {
    display: none;

    &.visibility-layout--desktop {
        @media @large-screen, @xl-screen {
            display: block;
        }
    }

    &.visibility-layout--tablet {
        @media @medium-screen {
            display: block;
        }
    }

    &.visibility-layout--mobile {
        @media @small-screen {
            display: block;
        }
    }
}
