@import '../base/variables';

.search-form {
    box-sizing: border-box;  width: 100%;  padding: 5px 10px;  border-radius: 10px;  background: rgba(255, 255, 255, .15);  text-aling: right;
    .search-form__search-input {
        min-width: 200px;         font-size: 16px;  color: white;  border: none;  border-bottom: 1px solid white;
        background: none;
        &::-webkit-input-placeholder {
            color: @text-color-2;
        }
        &::-moz-placeholder {
            color: @text-color-2;
        }
        /* Firefox 19+ */
        &:-moz-placeholder {
            color: @text-color-2;
        }
        /* Firefox 18- */
        &:-ms-input-placeholder {
            color: @text-color-2;
        }
    }

    .search-form__button {
        min-width: 50px;         font-size: 25px;  cursor: pointer;  color: white;  border: none;
        background: none;
    }

    @media @small-screen, @medium-screen {
        text-align: center;

        .search-form__search-input {
            width: 80%;
        }
    }
}
