@import '../base/variables';
@import '../base/layout';

.events {
    position: relative;  background-color: @background-color-3;

    .events__title-container {
        padding-bottom: 50px;  text-align: left;

        @media @small-screen {
            text-align: center;
        }
    }

    .events__title {
        display: inline-block;  min-width: 210px;  padding-bottom: 12px;  font-family: @main-control-font;  font-size: 24px;  text-transform: uppercase;  color: @text-color-1;  border-bottom: 1px solid @text-color-1;
    }

    .event__list {
        display: block;  margin: 0 auto;  padding-bottom: 40px;  font-size: 0;  text-align: center;  letter-spacing: 20px;
    }

    .events__footer {
        text-align: center;

        @media @small-screen {
            position: absolute;  bottom: 0;  left: 0;             width: 100%;
        }
    }
}

.event {
    display: inline-block;  box-sizing: border-box;  padding: 20px;  letter-spacing: normal;

    @media @small-screen {
        display: block;
    }

    .event__content {
        position: relative;

        display: block;  box-sizing: border-box;  width: 460px;  height: 282px;         cursor: pointer;
        @media @small-screen {
            width: auto;
            min-width: 250px;  max-width: 460px;  margin: 0 auto;
        }
    }

    .event__description-container {
        position: absolute;  top: 0;  left: 0;  display: table;  box-sizing: border-box;  width: 100%;  padding: 10px;  text-align: center;
    }

    .event__title-container {
        display: table-cell;  width: 100%;  height: 120px;  padding-bottom: 20px;  vertical-align: bottom;
    }

    .event__title {
        font-family: @main-control-font;  font-size: 22px;  font-weight: 400;  line-height: 24px;  text-transform: uppercase;  color: @text-color-2;
        &:hover {
            text-decoration: underline;
        }
    }

    .event__date-container {
        display: table-row;  width: 100%;  text-align: center;
    }

    .event__date {
        font-family: @main-control-font;  font-size: 18px;  color: @text-color-2;
        [class*='fa-'] {
            padding: 5px;
        }
    }
}
