@import '../base/variables';
@import '../base/layout';

.subscription {
    position: relative;  background: url(../images/forest-background.jpg) no-repeat;  background-size: cover;

    .subscription__inner-container {
        padding: 50px 0;
    }

    .subscription__title-container {
        width: 100%;  padding-bottom: 30px;  text-align: center;
        @media @small-screen {
            font-size: 16px;
        }
    }

    .subscription__title {
        font-family: @main-control-font;  font-size: 22px;  font-weight: bold;  text-transform: uppercase;  color: @text-color-2;
    }

    .subscription__form-container {
        width: 100%;  text-align: center;
    }

    .subscription__button-container {
        display: inline-block;  vertical-align: middle;

        @media @small-screen {
            position: absolute;  bottom: 0;  left: 0;             width: 100%;

            text-align: center;
        }
    }

    .subscription__email-container {
        display: inline-block;  height: 54px;  padding-right: 20px;

        @media @small-screen {
            width: 100%;  padding-right: 0;             padding-bottom: 20px;
        }
    }

    .subscription__email {
        box-sizing: border-box;  min-width: 300px;  height: 100%;  font-family: @main-control-font;  font-size: 14px;  font-weight: bold;  text-align: center;  color: @text-color-2;  border: 5px solid @text-color-2;  background-color: transparent;
        &:focus {
            outline-width: 0;
        }

        &::-webkit-input-placeholder {
            text-transform: uppercase;  color: @text-color-2;
        }
        &::-moz-placeholder {
            text-transform: uppercase;  color: @text-color-2;
        }
        /* Firefox 19+ */
        &:-moz-placeholder {
            text-transform: uppercase;  color: @text-color-2;
        }
        /* Firefox 18- */
        &:-ms-input-placeholder {
            text-transform: uppercase;  color: @text-color-2;
        }
    }

    @media @small-screen {
        min-height: 350px;
    }
}
